import React from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getVideogames } from "../../actions";
import s from "./home.module.css";

import { useState } from "react";
import Pagination from "../Pagination/Pagination";
import Filters from "../Filters/Filters";
import VgCard from "../vgCard/vgCard";
import Nav from "../Nav/Nav";
function Home() {
  const dispatch = useDispatch();
  const videogames = useSelector((state) => state.filtered);

  useEffect(() => {
    dispatch(getVideogames());
  }, []);

  const [currentPage, setCurrentPage] = useState(1);
  const [cardPerPage] = useState(15);
  const indexOfLastCard = currentPage * cardPerPage;
  const indexOfFirstCard = indexOfLastCard - cardPerPage;
  var currentCards;
  if (typeof videogames === "string") {
    currentCards = videogames;
  } else {
    currentCards = videogames.slice(indexOfFirstCard, indexOfLastCard);
  }

  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);
  };
  return (
    <div className={s.homeContainer}>
      <Nav />
      <Filters />
      <Pagination
        cardPerPage={cardPerPage}
        totalCards={videogames.length}
        paginate={paginate}
        currentPage={currentPage}
      ></Pagination>

      <div className={s.vgContainer}>
        {currentCards.length &&
          currentCards.map((g) => (
            <VgCard
              key={g.id}
              name={g.name}
              rating={g.rating}
              genres={g.genres}
              image={g.background_image}
              id={g.id}
            />
          ))}
      </div>
    </div>
  );
}

export default Home;
